<template>
  <div>
    <van-nav-bar title="后勤报修"/>
    <van-form @submit="onSubmit">
      <van-field
        label="区域"
        :value="area_val"
        :rules="[{ required: true, message: '请选择区域' }]"
        name="area"
        placeholder="点击选择"
        readonly
        @click="show_area = true"
      />
      <van-popup v-model="show_area" position="bottom">
        <van-picker
          :columns="area_columns"
          show-toolbar
          placeholder="['请选择', '请选择', '请选择']"
          @cancel="show_area = false"
          @confirm="area_onConfirm"
        />
      </van-popup>
      <van-field
        label="报修类型"
        :value="type_val"
        :rules="[{ required: true, message: '请选择报修类型' }]"
        name="room_repair_type"
        readonly
        placeholder="点击选择"
        @click="show = true"
      />
      <van-popup v-model="show" position="bottom">
        <van-picker :columns="columns" show-toolbar @cancel="show = false" @confirm="onConfirm" />
      </van-popup>
      <van-field
        name="room_repair_describe"
        v-model="fault"
        rows="2"
        autosize
        label="故障描述"
        :rules="[{ required: true, message: '请填写故障描述' }]"
        type="textarea"
        maxlength="30"
        placeholder="请输入"
        show-word-limit
      />
      <div class="upload-images">
        <div class="img-box" v-for="(item,index) in uploadImages" :key="index">
          <div class="icon-po">
            <img class="image" :src="item" @click="look_img(uploadImages,index)" />
            <div class="icon-close" @click="imgclose(index)">×</div>
          </div>
        </div>
        <van-uploader :after-read="onRead"></van-uploader>
      </div>
      <van-button round block type="info" native-type="submit">提交</van-button>
    </van-form>
  </div>
</template>

<script>
import axios from "axios";
import { apirepairtypelist } from "@/api/index";
import { apiarealist } from "@/api/index";
import { apilogisticsrepairsave } from "@/api/index";
import dropDown from "@/components/public/drop-down";
import { Toast } from 'vant';
import { ImagePreview } from 'vant';
export default {
  name: "logisticsRepair",
  data() {
    return {
      area_val: "",
      type_val: "",
      columns: [],
      area_columns:[],
      placeholder: "选择",
      show: false,
      show_area: false,
      fault: "",
      uploadImages: [],
      files: {
        name: "",
        type: ""
      },
    };
  },
  components: {
    dropDown
  },
  methods: {
    onConfirm(values) {
      if (typeof values !== "string") {
        values = values.toString();
      }
      this.type_val = values.split(",").join("-");
      this.show = false;
    },
    area_onConfirm(values) {
      if (typeof values !== "string") {
        values = values.toString();
      }
      this.area_val = values.split(",").join("-");
      this.show_area = false;
    },
    onSubmit(values) {
      this.$loading('提交中');
      values.image_src = this.uploadImages;
      values.image_src = this.uploadImages;
      if (values.image_src.length < 1) {
        Toast("请上传图片")
        this.$loading.close();
        return false;
      }
      apilogisticsrepairsave(values).then(res => {
        debugger
        if(res.data.code==200){
          this.$loading.close();
            Toast.success(res.data.msg);
            setTimeout(() =>{
               this.$router.push({
                    path:"/repairlist"
                });
            },3000);
        }else{
          this.$loading.close();
            Toast.fail(res.data.msg);
        }     
      });
    },
    look_img(img,index){
      ImagePreview({
        images:img,
        startPosition: index,
      });
    },
    getreparilist() {
      let params = {
        repair_type: "后勤"
      };
      apirepairtypelist(params).then(res => {
        this.columns = res.data.data;
      });
    },
    getareailist() {
      apiarealist().then(res => {
        this.area_columns = res.data.data;
      });
    },
    imgclose(e) {
      this.uploadImages.splice(e, 1);
    },
    // 组件方法 获取 流
    async onRead(file) {
      this.$loading('图片上传中...');
      let self = this;
      self.files.name = file.file.name; // 获取文件名
      self.files.type = file.file.type; // 获取类型
      self.imgPreview(file.file);
    },
    // 处理图片
    imgPreview(file) {
      let self = this;
      let Orientation;
      //去获取拍照时的信息，解决拍出来的照片旋转问题
      // EXIF.getData(file, function () {
      //   Orientation = EXIF.getTag(this, "Orientation");
      // });
      // 看支持不支持FileReader
      if (!file || !window.FileReader) return;
      if (/^image/.test(file.type)) {
        // 创建一个reader
        let reader = new FileReader();
        // 将图片2将转成 base64 格式
        reader.readAsDataURL(file);
        // 读取成功后的回调
        reader.onloadend = function() {
          // console.log(this.result);
          let result = this.result;
          let img = new Image();
          img.src = result;
          //判断图片是否大于500K,是就直接上传，反之压缩图片
          if (this.result.length <= 500 * 1024) {
            self.headerImage = this.result;
            self.postImg();
          } else {
            img.onload = function() {
              let data = self.compress(img, Orientation);
              self.headerImage = data;
              self.postImg();
            };
          }
        };
      }
    },
    // 压缩图片
    compress(img, Orientation) {
      let canvas = document.createElement("canvas");
      let ctx = canvas.getContext("2d");
      //瓦片canvas
      let tCanvas = document.createElement("canvas");
      let tctx = tCanvas.getContext("2d");
      // let initSize = img.src.length;
      let width = img.width;
      let height = img.height;
      //如果图片大于四百万像素，计算压缩比并将大小压至400万以下
      let ratio;
      if ((ratio = (width * height) / 4000000) > 1) {
        // console.log("大于400万像素");
        ratio = Math.sqrt(ratio);
        width /= ratio;
        height /= ratio;
      } else {
        ratio = 1;
      }
      canvas.width = width;
      canvas.height = height;
      //    铺底色
      ctx.fillStyle = "#fff";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      //如果图片像素大于100万则使用瓦片绘制
      let count;
      if ((count = (width * height) / 1000000) > 1) {
        // console.log("超过100W像素");
        count = ~~(Math.sqrt(count) + 1); //计算要分成多少块瓦片
        //      计算每块瓦片的宽和高
        let nw = ~~(width / count);
        let nh = ~~(height / count);
        tCanvas.width = nw;
        tCanvas.height = nh;
        for (let i = 0; i < count; i++) {
          for (let j = 0; j < count; j++) {
            tctx.drawImage(
              img,
              i * nw * ratio,
              j * nh * ratio,
              nw * ratio,
              nh * ratio,
              0,
              0,
              nw,
              nh
            );
            ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
          }
        }
      } else {
        ctx.drawImage(img, 0, 0, width, height);
      }
      //修复ios上传图片的时候 被旋转的问题
      if (Orientation != "" && Orientation != 1) {
        switch (Orientation) {
          case 6: //需要顺时针（向左）90度旋转
            this.rotateImg(img, "left", canvas);
            break;
          case 8: //需要逆时针（向右）90度旋转
            this.rotateImg(img, "right", canvas);
            break;
          case 3: //需要180度旋转
            this.rotateImg(img, "right", canvas); //转两次
            this.rotateImg(img, "right", canvas);
            break;
        }
      }
      //进行最小压缩
      let ndata = canvas.toDataURL("image/jpeg", 0.6);
      tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
      return ndata;
    },
    // 旋转图片
    rotateImg(img, direction, canvas) {
      //最小与最大旋转方向，图片旋转4次后回到原方向
      const min_step = 0;
      const max_step = 3;
      if (img == null) return;
      //img的高度和宽度不能在img元素隐藏后获取，否则会出错
      let height = img.height;
      let width = img.width;
      let step = 2;
      if (step == null) {
        step = min_step;
      }
      if (direction == "right") {
        step++;
        //旋转到原位置，即超过最大值
        step > max_step && (step = min_step);
      } else {
        step--;
        step < min_step && (step = max_step);
      }
      //旋转角度以弧度值为参数
      let degree = (step * 90 * Math.PI) / 180;
      let ctx = canvas.getContext("2d");
      switch (step) {
        case 0:
          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0);
          break;
        case 1:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, 0, -height);
          break;
        case 2:
          canvas.width = width;
          canvas.height = height;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, -height);
          break;
        case 3:
          canvas.width = height;
          canvas.height = width;
          ctx.rotate(degree);
          ctx.drawImage(img, -width, 0);
          break;
      }
    },
    //将base64转换为文件
    dataURLtoFile(dataurl) {
      var arr = dataurl.split(","),
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], this.files.name, {
        type: this.files.type
      });
    },
    //这里写接口
    async postImg() {
      let file = this.dataURLtoFile(this.headerImage);
      let formData = new window.FormData();
      formData.append("file", file);
      //toast_loding(this, "图片上传中···");
      try {
        axios({
          method: "post",
          url: "http://47.104.193.180:18086/fastfds-uploader/v3",
          data: formData
        }).then(res => {
          let str = res.data.url;
          this.uploadImages.push(str); //将图片地址存入一个数组中
          this.$loading.close();
        });
      } catch (e) {
        console.log(e);
      }
    }
  },
  created() {
    this.getreparilist();
    this.getareailist();
  }
};
</script>

<style>
</style>


